

import commander from 'commander';
import path from 'path';
import { readFileSync } from 'jsonfile';

import runTest from '../tester';

// Ensure NODE_ENV is set
process.env.NODE_ENV = process.env.NODE_ENV || 'test';

commander
  .option('-a, --app-code [code]', 'the code for your app, get from chromaticqa.com')
  .option(
    '-s, --script-name [name]',
    'How to start your app. Set to none if your app is already running.'
  )
  .option('-p, --port [port]', 'What port does your app serve?')
  .option('--app-path [path]', "Do we need to access a different path (instead of '/')?", '/')
  .option('--storybook-addon', 'Autodetect config for storybook addon setup', false)
  .option('--debug', 'Output more debugging information', false)
  .option('--create-tunnel [boolean]', 'tunnel the service over the internet (default true)', true)
  .option('--index-url [url]', 'index to connect to')
  .parse(process.argv);

function findOption(storybookScript, shortName, longName) {
  const parts = storybookScript.split(/[\s+|=]/);
  let index = parts.indexOf(longName);
  if (index === -1) {
    index = parts.indexOf(shortName);
  }
  if (index === -1) {
    throw new Error(
      `Didn't find storybook option: ${shortName}/${longName} in '${storybookScript}'`
    );
  }
  return parts[index + 1];
}

let storybookOptions = {};
if (commander.storybookAddon) {
  storybookOptions = {
    appPath: '/iframe.html',
  };

  const packageJson = readFileSync(path.resolve('./package.json'));
  const scriptName = commander.scriptName || 'storybook';
  const storybookScript = packageJson.scripts && packageJson.scripts[scriptName];

  if (storybookScript) {
    const port = findOption(storybookScript, '-p', '--port');

    let configDir = './.storybook';
    try {
      configDir = findOption(storybookScript, '-c', '--config-dir');
    } catch (e) {
      if (e.message.match('find storybook option')) {
        // eslint-disable-next-line no-console
        console.log(`Chromatic Tester: ${e.message}, using default ('${configDir}')`);
      } else {
        throw e;
      }
    }

    storybookOptions = {
      ...storybookOptions,
      scriptName,
      port,
    };

    // eslint-disable-next-line no-console
    console.log(
      `Chromatic Tester: Detected '${scriptName}' script, running on port ${port} and with config dir '${configDir}'`
    );
  } else {
    // eslint-disable-next-line no-console
    console.error(
      `Chromatic Tester: Didn't find a script called '${scriptName}' in your \`package.json\`.\n` +
        'Make sure you set the `--script-name` option to the value of the npm script that starts your storybook'
    );
    process.exit(255);
  }
}

const commandLineOptions = {
  config: commander.config,
  appCode: commander.appCode,
  scriptName: commander.scriptName,
  port: commander.port,
  appPath: commander.appPath,
  verbose: commander.debug,
  createTunnel: commander.createTunnel !== 'false',
  indexUrl: commander.indexUrl,
  ...storybookOptions,
};

runTest(commandLineOptions)
  .then(code => process.exit(code))
  .catch(e => {
    // eslint-disable-next-line no-console
    console.error(e);
    // Not sure what exit code to use but this can mean error.
    process.exit(255);
  });
